import { Button, Card, TextField } from "@material-ui/core";
import React from "react";
import useForm from "../../hooks/useForm";
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';

const formVacio = {
  nombre: "",
  importe: "",
};

const Alerta = () => {
  const { form, handleChange, limpiarForm } = useForm({ ...formVacio });
  // const classes = useStyles();

  return (
    <div style={{ display: "flex", flexDirection: 'column', gap: "50px" }}>
      <div style={{ display: "flex", gap: "12px" }}>
        <TextField
          label="Nombre"
          value={form.nombre}
          onChange={handleChange("nombre")}
        />
        <TextField
          label="Importe"
          value={form.importe}
          onChange={handleChange("importe")}
        />
      </div>
      <Card variant="outlined"
      // className={classes.modal}
      style={{width: '450px', display: 'flex', flexDirection: 'column', gap: "8px", padding: '12px', borderRadius: '8px', borderColor: '#888'}}
      >
        <div style={{ display: "flex", gap: "12px", flexWrap: 'wrap', justifyContent: 'center', alignContent: 'center' }}>
          <ErrorOutlineIcon color="error" style={{height: '32px', width: '32px'}}/>
          <h2>Ocurrio un error</h2>
        </div>
        <p style={{fontWeight: 'bold'}}>Descripcin del error:</p>
        <p>{`No se puede crear el pedido, el cliente ${form.nombre || ''} tiene una deuda de S/.${form.importe || ''}`}</p>
        <Button style={{alignSelf: 'flex-end', marginTop: '6px'}} onClick={limpiarForm}>CERRAR</Button>
      </Card>
    </div>
  );
};

export default Alerta;
