import axios from 'axios'
import { URL_BACKEND_LOCAL } from '../environments/environment'

export const uploadOrderSheet = async (file) => {
    let formData = new FormData();
    formData.append("orderSheetPdf", file)
    const rpta = await axios.post(`${URL_BACKEND_LOCAL}/uploadOrderSheet`,
        formData,
        {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    return rpta
}

export const postOrderSheetData = async (objOrderSheetData) => {
    const rpta = await axios.post(`${URL_BACKEND_LOCAL}/orderSheet`,
        JSON.stringify(objOrderSheetData),
        {
            headers: {
                'Content-Type': 'application/json'
            }
        })
    return rpta
}

export const saveOrders = async (obj) => {
    const rpta = await axios.post(`${URL_BACKEND_LOCAL}/saveOrders`,
        JSON.stringify(obj),
        {
            headers: {
                'Content-Type': 'application/json'
            }
        })
    return rpta
}