import { useEffect, useState } from "react";
import { getClientes, getClientesActive, getClientesPaymentSheet } from "../services/clienteService";

const useClientes = (mode='all') => {
  const [clientes, setClientes] = useState([])

  const addCliente = (objCliente) => {
    setClientes([...clientes, objCliente])
  }

  const loadClientesActive = () => {
    getClientesActive().then((rpta) => {
      if (rpta.request.status === 200) {
        setClientes(rpta.data);
        localStorage.setItem('clientesActive', JSON.stringify(rpta.data));
      }
    }).catch((err) => {
      if(localStorage.getItem('clientesActive')) {
        setClientes(JSON.parse(localStorage.getItem('clientesActive')))
      }
    });
  }

  const loadClientesPaymentSheet = () => {
    getClientesPaymentSheet().then((rpta) => {
      if (rpta.request.status === 200) {
        setClientes(rpta.data);
        localStorage.setItem('clientesPaymentSheet', JSON.stringify(rpta.data));
      }
    }).catch((err) => {
      if(localStorage.getItem('clientesPaymentSheet')) {
        setClientes(JSON.parse(localStorage.getItem('clientesPaymentSheet')))
      }
    });
  }

  const loadAllClientes = () => {
    getClientes().then((rpta) => {
      if (rpta.request.status === 200) {
        setClientes(rpta.data);
        localStorage.setItem('clientes', JSON.stringify(rpta.data));
      }
    }).catch((err) => {
      if(localStorage.getItem('clientes')) {
        setClientes(JSON.parse(localStorage.getItem('clientes')))
      }
    });
  }

  useEffect(() => {
    // if(active) loadClientesActive()
    // else loadAllClientes()
    switch (mode) {
      case 'active':
        loadClientesActive()
      break

      case 'payment':
        loadClientesPaymentSheet()
      break

      default: 
        loadAllClientes()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return {
    clientes,
    addCliente,
    loadAllClientes,
    loadClientesActive,
    loadClientesPaymentSheet
  }
}

export default useClientes