import axios from 'axios';
import { URL_BACKEND } from '../environments/environment';

export const getOrderSheetByDate = async (fecha) => {
  const rpta = await axios.get(`${URL_BACKEND}/orderSheet?fecha=${fecha}`)
  return rpta
}

export const postOrderSheet = async (objOrderSheet) => {
  const rpta = await axios.post(`${URL_BACKEND}/orderSheet`,
    JSON.stringify(objOrderSheet),
    {
      headers: {
        'Content-Type': 'application/json'
      }
    }
  )
  return rpta;
}

export const putOrderSheet = async (objOrderSheet) => {
  const rpta = await axios.put(`${URL_BACKEND}/orderSheet/${objOrderSheet.id}`,
    JSON.stringify(objOrderSheet),
    {
      headers: {
        'Content-Type': 'application/json'
      }
    }
  )
  return rpta;
}
