import {
  Button,
  FormControlLabel,
  Switch,
  TextField,
  InputAdornment
} from "@material-ui/core";
import React from "react";
import ModalForm from "../../../components/ModalForm";
import useForm from "../../../hooks/useForm";
import { postCliente } from "../../../services/clienteService";
import useOpen from "../hooks/useOpen";

const formVacio = {
  nombre: "",
  nombrePrograma: "",
  price: "",
  estado: true,
  paymentSheet: true,
  joinActive: true,
  camal: "",
  client: ""
};

const ModalCliente = ({ addCliente }) => {

  const { form, handleChange, limpiarForm } = useForm({ ...formVacio });
  const { open, handleOpen } = useOpen();

  const handleSubmit = (e) => {
    e.preventDefault();
    postCliente({ ...form }).then((rpta) => {
      if (rpta.request.status === 201) {
        handleOpen();
        limpiarForm();
        addCliente(rpta.data);
      }
    });
  };

  return (
    <>
      <div>
        <Button variant="contained" color="primary" onClick={handleOpen}>
          Nuevo
        </Button>
      </div>
      <ModalForm
        open={open}
        handleOpen={handleOpen}
        handleSubmit={handleSubmit}
      >
        <TextField
          label="Nombre"
          value={form.nombre}
          onChange={handleChange("nombre")}
        />
        <TextField
          label="Camal"
          value={form.camal}
          onChange={handleChange("camal")}
        />
        <TextField
          label="Nombre Programa"
          value={form.nombrePrograma}
          onChange={handleChange("nombrePrograma")}
        />
        <TextField
          label="Precio extra"
          type="number"
          value={form.price}
          onChange={handleChange("price")}
          InputProps={{
            startAdornment: <InputAdornment position="start">S/.</InputAdornment>,
          }}
        />
        <TextField
          label="Pertenece a"
          value={form.client}
          onChange={handleChange("client")}
        />
        <FormControlLabel
          control={
            <Switch
              checked={form.estado}
              onChange={handleChange("estado")}
              name="estado"
              color="primary"
            />
          }
          // label={form.estado ? "Activo" : "Desactivo"}
          label="Hoja pedidos"
        />
        <FormControlLabel
          control={
            <Switch
              checked={form.paymentSheet}
              onChange={handleChange("paymentSheet")}
              name="paymentSheet"
              color="primary"
            />
          }
          // label={form.paymentSheet ? "Activo" : "Desactivo"}
          label="Hoja pagos"
        />
        <FormControlLabel
          control={
            <Switch
              checked={form.joinActive}
              onChange={handleChange("joinActive")}
              name="joinActive"
              color="primary"
            />
          }
          // label={form.joinActive ? "Sumar" : "No sumar"}
          label="Sumar"
        />
        <Button variant="contained" color="primary" type="submit">
          Guardar
        </Button>
        <Button
          variant="outlined"
          color="primary"
          onClick={() => {
            limpiarForm();
            handleOpen();
          }}
        >
          Cancelar
        </Button>
      </ModalForm>

    </>
  );
};

export default ModalCliente;
