import {
    TextField,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow
} from "@material-ui/core";
import { Alert } from '@material-ui/lab';
import React, { useRef, useState } from "react";
import { postOrderSheetData, uploadOrderSheet, saveOrders } from "../../services/orderSheetService";

const emptyOrderSheetData = {
    orderDate: "",
    costPrice: "",
    nameArchive: ""
}

const ImportarPedidos = () => {

    const [orderSheetData, setOrderSheetData] = useState(emptyOrderSheetData);
    const [enableButtonSave, setEnableButtonSave] = useState(true);
    const [dataOrders, setDataOrders] = useState({
        totalQuantity: 0,
        totalWeight: 0,
        totalCost: 0
    });
    const [orders, setOrders] = useState([]);
    const [alert, setAlert] = useState({
        text: "",
        active: false
    });
    const sheetRef = useRef();

    // useEffect(() => {
    //     if(localStorage.getItem("ordersOfOrdersSheet")) {
    //         setOrders(JSON.parse(localStorage.getItem("ordersOfOrdersSheet")))
    //     }
    // }, [])

    const handleChange = (e) => {
        setOrderSheetData({
            ...orderSheetData,
            [e.target.name]: e.target.value
        })
    }

    const sendOrderSheet = () => {
        if (orderSheetData.orderDate === "" || orderSheetData.costPrice === "" || sheetRef.current.files.length === 0) {
            setAlert({
                text: "Por favor inserte todos los datos.",
                active: true
            })
            return
        }
        setAlert({
            text: "",
            active: false
        })
        const orderSheetPdf = sheetRef.current.files[0];
        uploadOrderSheet(orderSheetPdf).then((res) => {
            const nameArchive = res.data.nameArchive;
            setOrderSheetData({
                ...orderSheetData,
                nameArchive: nameArchive
            })
            postOrderSheetData({ ...orderSheetData, nameArchive: nameArchive }).then((res) => {
                if (res.status) {
                    if (res.data.length === 0) {
                        setAlert({
                            text: "Ocurrio un error, por favor inserte correctamente la fecha del pedido.",
                            active: true
                        })
                    } else {
                        localStorage.setItem("ordersOfOrdersSheet", JSON.stringify(res.data))
                        setOrders(res.data.orders)
                        setDataOrders(res.data.data)
                    }
                }
            })
        })
    }

    const handleSaveOrders = () => {
        setEnableButtonSave(false)
        saveOrders({orders: orders})
    }

    return (
        <>
        <div style={{
            width: "300px",
            display: "flex",
            flexDirection: "column",
            gap: "20px"
        }}>
            {alert.active && <Alert severity="error">{alert.text}</Alert>}
            <TextField
                label="Fecha de pedido"
                type="date"
                name="orderDate"
                value={orderSheetData.orderDate}
                InputLabelProps={{
                    shrink: true,
                }}
                onChange={handleChange}
            />
            <TextField
                label="Precio de costo"
                type="number"
                name="costPrice"
                value={orderSheetData.costPrice}
                onChange={handleChange}
                InputLabelProps={{
                    shrink: true,
                }}
            />
            <TextField
                label="Subir hoja de pedidos"
                type="file"
                inputRef={sheetRef}
                InputLabelProps={{
                    shrink: true,
                }}
            />
            <Button variant="contained" color="primary" onClick={sendOrderSheet}>Importar pedidos</Button>
            </div>
            {orders.length !== 0 && orderSheetData.nameArchive &&
                <>
                <TableContainer>
                    <Table size="small" aria-label="a dense table">
                        <TableHead>
                            <TableRow>
                                {["Nombre", "Cantidad", "KG", "Precio", "Nombre Programa"].map((cellHead) => (
                                    <TableCell key={cellHead}>{cellHead}</TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {orders.map((order, i) => (
                                <TableRow key={`row${i}`}>
                                    {["customerName", "quantity", "weight", "price", "nombrePrograma"].map((column, j) => {
                                        return (
                                            <TableCell key={`column${i}${j}`}>
                                                {order[column]}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            ))}
                            {
                                <TableRow>
                                    <TableCell>Costo Total</TableCell>
                                    <TableCell>{dataOrders.totalQuantity}</TableCell>
                                    <TableCell>{dataOrders.totalWeight}</TableCell>
                                    <TableCell>{dataOrders.totalCost}</TableCell>
                                </TableRow>
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
                <Button variant="contained" color="primary" onClick={handleSaveOrders} disabled={!enableButtonSave}>Guardar pedidos</Button>
                </>
            }
        </>
    );
};

export default ImportarPedidos;
